import { Box } from '@mui/material'
import React from 'react'
import { Outlet } from "react-router-dom";
import { NavBar } from '../navbar'
import * as sx from "./styles";

export const NavBox = () => {
  return (
    <Box sx={sx.navBoxStyle} >
      <NavBar />
      <Outlet />
    </Box>
  )
}
