import React, { FC } from 'react';
import { NavLink } from 'react-router-dom';
import { Box } from '@mui/system';
import { Link } from '@mui/material';
import { headerLinkStyles, linkStyles, StyledNavLink } from './styles';

type THeaderNavLinkPropTypes = {
  to: string;
  name: string;
  children: React.ReactElement;
};

export const HeaderNavLink: FC<THeaderNavLinkPropTypes> = ({ to, children, name }) => {
  return (
    <Link component={NavLink} to={to} underline='none' sx={headerLinkStyles}>
      <Box sx={linkStyles}>
        {children}
        <StyledNavLink>{name}</StyledNavLink>
      </Box>
    </Link>
  );
};
