import React, { FC, SVGProps } from "react";
import { Box } from "@mui/system";
import { HeaderNavLink } from "./HeaderNavLink";
import { headerNavBoxStyles } from "./styles";

// import { AccountIcon } from '../../icons/AccountIcon';
import { LocationIcon } from "../../icons/LocationIcon";
import CurrencyIcon from "../../icons/CurrencyIcon";
import PhoneIcon from "../../icons/PhoneIcon";
// import { useAppSelector } from 'hooks/useAppSelector';
// import { getAccountUserName, getAccountStatus } from 'store/reducers/accountSlice';

type TNavIconPropsTypes = {
  props?: SVGProps<SVGSVGElement>;
};

type THeaderNavContentTypes = {
  path: string;
  name: string;
  icon: FC<TNavIconPropsTypes>;
};

export const HeaderNav = () => {
  // const accountUserName = useAppSelector(getAccountUserName);
  // const hasAccess = useAppSelector(getAccountStatus);
  const hasAccess = true;

  const headerNavContent: THeaderNavContentTypes[] = [
    {
      path: "departmentsPage",
      name: "Банкоматы и отделения",
      icon: LocationIcon,
    },
    {
      path: "currencyPage",
      name: "Курсы валют",
      icon: CurrencyIcon,
    },
    {
      path: "navbar",
      name: "Навигация",
      icon: PhoneIcon,
    },
    // {
    //   path: ROUTES.settingsPage,
    //   name: `${accountUserName}`,
    //   icon: AccountIcon
    // }
  ];

  const statusHeaderNavContent = hasAccess
    ? headerNavContent
    : headerNavContent.slice(0, 3);

  return (
    <Box component="nav" sx={headerNavBoxStyles}>
      
      {statusHeaderNavContent.map((item) => {
        const NavIcon = item.icon;
        return (
          <HeaderNavLink
            to={item.path}
            name={item.name}
            key={item.name}
          >
            <NavIcon />
          </HeaderNavLink>
        );
      })}
    </Box>
  );
};
