import React from "react";
import { RouteObject, useRoutes } from "react-router-dom";
import App from "../App";
import { Cards } from "../components/Cards";
import { NavBox } from "../components/NavBox";
import { NavBoxDefaultPage } from "../components/NavBox/navBoxPages/NavBoxDefaultPage";
import { NavBoxFirstPage } from "../components/NavBox/navBoxPages/NavBoxFirstPage";
import { NavBoxProfilePage } from "../components/NavBox/navBoxPages/NavBoxProfilePage";
import { TestPage } from "../components/NavBox/navBoxPages/TestPage";
import { MainPage } from "../pages/MainPage";

export const AppRouter = () => {
  const routes: RouteObject[] = [
    {
      path: "/",
      element: <App />,
      children: [
        { index: true, element: <MainPage /> },
        { path: "departmentsPage", element: "departmentsPage" },
        { path: "currencyPage", element: <Cards /> },
        {
          path: "navbar",
          element: <NavBox />,
          children: [
            { index: true, element: <NavBoxDefaultPage /> },
            { path: "account/:id", element: <TestPage /> },
            { path: "profile", element: <NavBoxProfilePage /> },
            { path: "recovery", element: "recovery" },
            { path: "departments", element: "departments" },
            {
              path: "account",
              element: <NavBoxFirstPage />,
              // children: [
              //   // {index: true, element: <> <h3>111111</h3> </> },
              //   {path: "01", element: <> <h3>1111111111</h3> </>}
              // ]
            },
          ],
        },
        // {
        //   path: 'account',
        //   element: '<AccountStatusValidator protectedPage={<AccountPage />} />',
        //   children: [
        //     { index: true, element: '<UserAccountMainMenu />' },
        //     { path: 'cards', element: <Cards/> },
        //     {
        //       path: 'deposits',
        //       element: '<DepositsPage />',
        //       children: [
        //         { index: true, element: '<Deposits />' },
        //         { path: 'details/:id', element: '<BankProductDetails />' },
        //         { path: 'order/:id', element: '<BankProductOrder />' },
        //         { path: 'conditions/:id', element: '<BankProductConditions />' }
        //       ]
        //     },
        //     { path: 'loans', element: 'Кредиты' },
        //     { path: 'insurance', element: 'Страхование' },
        //     { path: 'investments', element: 'Инвестиции' },
        //     { path: 'deals', element: 'Предложения' }
        //   ]
        // }
      ],
    },
  ];

  return useRoutes(routes);
};
