import { styled, Typography } from '@mui/material';
import { theme } from '../../../../theme';

export const StyledNavLink = styled(Typography)(() => ({
  ml: 1.25,
  position: 'relative',
  // color: 'teal',
  // color: headerTheme === 'dark' ? theme.palette.primary.darkGrey : theme.palette.common.white,
  color: theme.palette.primary.darkGrey,
  transition: '0.3s linear',
  '&:hover': {
    boxShadow: `0 1px 0 0 ${
      // headerTheme === 'dark' ? theme.palette.primary.darkGrey : theme.palette.primary.main
      theme.palette.primary.darkGrey
    }`
  }
}));

export const linkStyles = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  gap: 1.25,
  '&.active': {
    span: {
      fontWeight: 500
    }
  }
};

export const headerLinkStyles = {
  ml: 3.5,
};
